import React from 'react';
import './App.css';
import 'katex/dist/katex.min.css'
import Latex from 'react-latex-next'
import examples from './examples'
import { Button, Container, Col, Row } from 'react-bootstrap'
import Work from './Work'
import image5 from './images/Images-05.png'
import image8 from './images/Images-08.png'
import image9 from './images/Images-09.png'


class App extends React.Component {
  constructor(){
    super();
    this.state = {
      currentExample: 0,
      currentStep: 0,
      showBearings: true,
    }
    this.previousExample = this.previousExample.bind(this)
    this.nextExample = this.nextExample.bind(this)
    this.revealStep = this.revealStep.bind(this)
    this.setExample = this.setExample.bind(this)
    this.showBearings = this.showBearings.bind(this)
  }

  previousExample(){
    if (this.state.currentExample !== 0){
      this.setState({currentExample: this.state.currentExample - 1, currentStep: 0})
      
    }
  }

  nextExample(){
    if (this.state.currentExample < examples.length - 1){
      this.setState({
        currentExample: this.state.currentExample + 1,
        currentStep: 0
      })
    }
  }

  revealStep(){
    this.setState({currentStep: this.state.currentStep + 1, showMe: false})
  }

  setExample(value){
    this.setState({currentExample: value, currentStep: 0})
  }

  showBearings(){
    this.setState({showBearings: !this.state.showBearings})
    console.log('showbearings')
  }
   

  render(){

    const example = examples.map( (i, index_i) => {
      const solution = i.solution.map( (j, index_j) => {
          const work = j.work.map(k => <Work item = {k}/>)

          return(
            <div>
              {this.state.currentStep > index_j ?
            <React.Fragment>
              <h5 style={{marginTop: '1em'}}>Step {index_j + 1}</h5>
              <p><Latex>{j.instruction}</Latex></p>
              {work}
            </React.Fragment>
              : null
              }
              
            </div>
          )
      })

      return(
        <div style={{marginTop: '1em'}}>
        {this.state.currentExample === index_i ? 
        <React.Fragment>
          <h3>Example {index_i + 1}</h3>
          <p><Latex>{i.example}</Latex></p>
          <h4>Solution</h4>
          {solution}
          {this.state.currentStep < i.solution.length ? <Button style = {{marginTop: '1em'}} variant="outline-secondary" onClick = {this.revealStep}>Show step {this.state.currentStep + 1}</Button> : null}
        </React.Fragment> : null
        }
        </div>
      )
    }

    )

    const bearings = <div style={{marginTop: '1em', borderBottom: '1px solid #cccccc'}}>
                        <p>A bearing has 3 parts: A letter (N or S), followed by an acute angle, then a letter (E or W).</p>
                        <p>The following images illustrate how to represent an angle as a bearing.</p>
                        <Row>
                        <Col><img style = {{width: '300px'}} src={image5} alt='bearing'/></Col>
                        <Col><img style = {{width: '300px'}} src={image8} alt='bearing'/></Col>
                        <Col><img style = {{width: '300px'}} src={image9} alt='bearing'/></Col>
                        </Row>
                    </div>

    return(
      <Container style={{padding: 20}}>
          <Row style = {{borderBottom: '1px solid #cccccc'}}>
            <Col><h5>Jump to:</h5></Col>
            <Col onClick = {this.setExample.bind(this, 0)} style={{cursor: 'pointer'}}><h5>Example 1</h5></Col>
            <Col onClick = {this.setExample.bind(this, 1)} style={{cursor: 'pointer'}}><h5>Example 2</h5></Col>
            <Col onClick = {this.setExample.bind(this, 2)} style={{cursor: 'pointer'}}><h5>Example 3</h5></Col>
            <Col onClick = {this.setExample.bind(this, 3)} style={{cursor: 'pointer'}}><h5>Example 4</h5></Col>
          </Row>
          {this.state.currentExample === 3 ? (<div onClick = {this.showBearings}><h5 style={{cursor: 'pointer', marginTop: '1em'}}>{this.state.showBearings ? 'Info on Bearings (click here to hide)' : 'Show Bearings Info'}</h5></div>) : null}
          {this.state.showBearings && this.state.currentExample === 3 ? <div>{bearings}</div> : null}
          {example}
          {this.state.currentExample === 0 ? null : <Button style = {{marginTop: '1em', marginRight: '1em'}} onClick = {this.previousExample}>Previous Example</Button>}
          {this.state.currentExample === examples.length - 1 ? null : <Button style = {{marginTop: '1em'}} onClick = {this.nextExample}>Next Example</Button>}
      </Container>
    )
  }
}

export default App;
